ul {
  list-style-type: none;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
    
  }
  75% {
    transform: rotate(360deg);
    
  }
  100% {
    transform: rotate(720deg);
  }

}


/* stater holds 2 svg arrows, meanwhile this div will look like pulse */
.starterBtn{
  transition: transform 1s; /* Animation */
  transition: background-color 1s; /* Animation */
  border-radius: 50% 33% 50% 33%;
  animation: pulse 2s infinite linear;
}

.svgStarter{
  width: 10vmin;
  height: 10vmin;
  -webkit-transition: all 2s ease;  
  -moz-transition: all 2s ease;  
  -o-transition: all 2s ease;  
  -ms-transition: all 2s ease;  
  transition: all .5s ease;
  position: relative;
  top: 0%;
  right: 0%;
  bottom: 0%;
  left: 0%;
  display: initial;
}

.svgStarterLeftAtive{
  left: -100vw;
}
.svgStarterRightAtive{
  left: 100vw;
}

/* pause animation to show that you can click on the div */
.starterBtn:hover{
  cursor: pointer;
  transform: scale(1.1);
  background-color: rgba(0, 0, 0, 0.431);
  animation-play-state: paused;
}

/* making it look like pulse*/
@keyframes pulse{
  0% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
	}

	70% {
		transform: scale(1);
		box-shadow: 0 0 1920px 1920px rgba(0, 0, 0, 0);
	}

	100% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
	}
}





.StartingPage-enter {
  opacity: 0;
  transform: scale(0.9);
  transition: box-shadow 250ms;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
  border-radius: 50% 33% 50% 33%;
}
.StartingPage-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
  transition: box-shadow 500ms;
  box-shadow: 0 0 1920px 1920px rgba(0, 0, 0, 0);
}
.StartingPage-exit {
  opacity: 1;
  display: none;
}
.StartingPage-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}


.JustFading-enter {
  opacity: 0;
  transform: scale(0.9);
}
.JustFading-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}
.JustFading-exit {
  opacity: 1;
  display: none;
}
.JustFading-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}



.fade-in {
  opacity: 1;
  transition: opacity 1.5s ease;
}
.fade-out {
  opacity: 0;
  transition: opacity 1.5s ease;
}


.navbar-nav a{
  margin: 15px;
  text-decoration: none; /* no underline */
  color: inherit;
}

.navbar-nav a:hover{
  transform: scale(1.1);
  color: #61dafb;
}


.list-group .list-group-item{
  background-color: #001a3338;
  font-size: large;
}